:root {
  --black-color: #000000;
  --white-header-color: #ffffff;
  --red-header-color: #a91e1e;
  --yellow-color: #ebd873;
}

@font-face {
  font-family: "Gaming";
  src: url("../fonts/batmfa__-webfont.woff") format("woff"),
    url("../fonts/batmfa__-webfont.woff2") format("woff2");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  font-weight: bold;
  font-size: 14px;
  font-family: 'Gaming';
}

body {
  width: 100%;
  text-align: center;
  font-family: "Gaming";
  overflow-x: hidden;
}

h1 {
  font-weight: 500;
}

i {
  position: relative;
  margin-right: 20px;
  font-size: 25px;
}

i > .cart-amount {
  position: absolute;
  left: 25px;
  bottom: -10px;
  background-color: var(--red-header-color);
  color: var(--white-header-color);
  border-radius: 50%;
  font-size: .8vw;
  padding: 6px 6px;
}

@keyframes move {
  0% {
    left: 100%;
  }
}

@keyframes dissapearOnScreen {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-1600px);
  }
}

@keyframes appearOnScreen {
  0% {
    transform: scaleX(1400px);
  }
  100% {
    transform: scaleX(0);
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes dissapear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* ------ Header ------ */

.header {
  min-height: 20vh;
  background-color: var(--white-header-color);
}

.moving-header {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--black-color);
  min-height: 8vh;
  overflow-x: hidden;
}

.moving-header > h4 {
  position: absolute;
  color: var(--white-header-color);
  font-family: sans-serif;
  position: relative;
  left: -88%;
  animation: move 30s linear infinite;
}

.main-header {
  display: flex;
  min-height: 15vh;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-right: 100px;
  padding-left: 100px;
}

.header-name {
  display: flex;
  align-items: center;
}

.logo {
  max-width: 60px;
  margin-right: 5px;
}
.main-navbar {
  display: flex;
  align-items: center;
  min-height: calc(30vh - 8vh - 15vh);
}

.main-navbar > ul {
  display: flex;
  width: 100%;
  justify-content: space-around;
  font-weight: bold;
  list-style: none;
}

.main-navbar > ul > li {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
}

.main-navbar > ul > li:hover {
  border-bottom: 2px solid var(--black-color);
  padding-bottom: 8px;
}

/* ------ Home ------ */

.home {
  min-height: 300vh;
}

.home > img {
  width: 100%;
  height: 80vh;
}

.home > h2 {
  font-size: 3vw;
  margin: 50px 100px;
  letter-spacing: 5px;
  line-height: 60px;
}

.sections {
  margin: 0 auto;
  min-height: 120vh;
  max-width: 90vw;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  grid-template-rows: 1fr 1fr;
  gap: 35px;
}

.section {
  position: relative;
  transition: filter 0.15s ease;
}

.section:hover {
  filter: contrast(1.5);
}

.section > img {
  height: 100%;
  width: 100%;
}

.component {
  display: hidden;
  top: 220px;
  letter-spacing: 5px;
  left: 150px;
  position: absolute;
}

.component:hover {
  display: block;
  animation: appear 0.2s ease forwards;
}

.component:not(:hover) {
  animation: dissapear 0.2s ease forwards;
}

.component > h3 {
  font-size: 3vw;
  -webkit-text-stroke: 2px var(--black-color);
  color: var(--yellow-color);
  text-shadow: 0px 1px 1px 0px var(--black-color);
}

.mothers {
  left: 200px;
}

.video-cards {
  left: 140px;
}

.RAM {
  left: 280px;
}

.component-btn {
  font-family: "Gaming";
  background-color: var(--yellow-color);
  border: 3px solid var(--black-color);
  margin-top: 5px;
  padding: 5px 0;
  width: 10vw;
  cursor: pointer;
}

/* ------ Footer ------ */

.footer {
  display: flex;
  flex-direction: column;
  justify-content:space-evenly;
  min-height: 20vh;
  color: var(--white-header-color);
  background-color: rgb(0, 0, 0);
}

.social-media-links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 65px;
}

.social-media-links > i {
  font-size: 35px;
}

/* ------ Shop ------ */

.shop {
  background-color: var(--white-header-color);
  display: grid;
  gap: 25px;
  grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
  justify-items: center;
  padding: 25px;
}

.item {
  height: 420px;
  color: var(--white-header-color);
  background-color: var(--black-color);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
}

.item > h4 {
  text-align: center;
  line-height: 20px;
  padding: 0 20px;
}

.item > img {
  padding: 8px;
  width: 100%;
  height: 200px;
}

.item-price {
  color: var(--yellow-color);
}

.item-buttons {
  margin-top: 15px;
  display: flex;
  align-items: center;
  gap: 25px;
}

.item-buttons > .amount-button {
  width: 60px;
  border-radius: 3px;  
  background-color: var(--white-header-color);
  border: 2px solid var(--white-header-color);
}

.add-button {
  margin-top: 15px;
  background-color: var(--yellow-color);
  width: 150px;
  padding: 5px 10px;
}

/* ------ Shopping Cart ------ */

.shopping-cart {
  min-height: 100vh;
  padding-top: 25px;
}

.shopping-cart > h2 {
  background-color: var(--black-color);
  color: var(--white-header-color);
  font-size: 3vw;
  padding: 25px 0;
}

.shopping-cart-product {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.shopping-cart-product > i {
  cursor: pointer;
  font-size: 20px;
  vertical-align: 3px;
}

.shopping-cart-product > i::before {
  vertical-align: -4px;
}

.shopping-cart-product > i:hover {
  color: var(--red-header-color);
}

.shopping-cart-product {
  margin: 50px 0;
  font-size: 1.5vw;
}

.shopping-cart-total-price {
  border-top: 2px solid black;
  font-size: 25px;
  margin: 0 200px;
  padding-top: 30px;
  margin-top: 25px;
}

form {
  margin: 50px auto;
  width: 35%;
  background-color: var(--black-color);
  color: var(--white-header-color);
  padding: 25px 15px;
  border-radius: 5px;
}

li input {
  padding: 3px 5px;
}

.shopping-cart select {
  width: 176px;
  padding: 3px 5px;
}

.shopping-cart ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.shopping-cart li {
  display: flex;
  gap: 25px;
}

.shopping-cart li > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form-credit-card > input {
  width: 378px;
}

form > button {
  margin-top: 15px;
  background-color: var(--yellow-color);
  width: 150px;
  padding: 5px 10px;
}

/* ------ Media Queries ------ */

@media (max-width: 360px) {
  
  .moving-header {
    min-height: 5vh;
    font-size: 10px;
    gap: 50px;
    width: 101%;
  }

  .moving-header > h4 {
    left: -98%;
    animation: move 30s linear infinite;
  }

  .logo {
    width:30px;
  }

  h1 {
    font-size: 20px;
  }

  i {
    font-size: 15px;
    margin-right: 10px;
  }

  .main-header {
    min-height: 10vh;
    padding: 0 30px;
  }

  i > .cart-amount {
    font-size: 8px;
    left: 15px;
    padding: 4px 7px;
  }

  .main-navbar {
    min-height: 5vh;
    font-size: 10px;
  }

  /* ------ home ------ */

  .home {
    min-height: 0;
  }

  .home > img {
    max-height: 30vh;
  }

  .home > h2 {
    margin: 20px 20px;
    line-height: 25px;
  }

  .sections {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .section > img {
    max-width: 100%;
  }

  /* ------ shopping cart ------ */

  .shopping-cart-product {
    display: flex;
    align-items: center;
    font-size: 9px;
  }

  .shopping-cart-product > i {
    font-size: 10px;
  }

  .shopping-cart-total-price {
    margin: 30px 25px;
    font-size: 16px;
  }

  form {
    width: 85%;
  }

  form ul {
    align-items: none;
  }

  .shopping-cart li {
    display: block;
  }

  .shopping-cart li div {
    margin-top: 10px;
  }

  .shopping-cart li input {
    width: 100%;
  }

  form button {
    margin-top: 30px;
  }

  /* ------ footer ------ */

  .footer {
    min-height: 10vh;
    font-size: 10px;
  }

  .footer i {
    font-size: 15px;
  }

}